.container,
.container-sm,
.container-md,
.container-lg {
    width: 100%;
    margin: 0 auto;
    padding: var(--streamvave-spacing-0) var(--streamvave-spacing-20);
}

.container {
    max-width: 100%;
}

.container-sm {
    max-width: 1200px;
}

.container-md {
    max-width: 1550px;
}

.container-lg {
    max-width: 1700px;
}

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    backface-visibility: hidden;
    transform-style: preserve-3d;
    transform: scale(1);
    cursor: pointer;
    padding: var(--streamvave-spacing-12) var(--streamvave-spacing-12);
    border-radius: var(--streamvave-border-radius-medium);
    // background-color: var(--streamvave-color-black-rgba-005);
    // border: 1px solid;
    // border-color:  rgba(0, 0, 0, 0.06);
    transition: var(--streamvave-transition-15s);

    &:active {
        transform: scale(0.97);
    }

    @media(hover: hover) {
        &:hover {
            background-color: var(--streamvave-color-black-rgba-010);
        }
    }

    @media(hover: none) {
        &:active {
            background-color: var(--streamvave-color-black-rgba-010);
        }
    }
}

.btn-fill {
    display: flex;
    align-items: center;
    justify-content: center;
    backface-visibility: hidden;
    transform-style: preserve-3d;
    transform: scale(1);
    cursor: pointer;
    color: var(--streamvave-color-white);
    background-color: var(--streamvave-color-black);
    padding: var(--streamvave-spacing-12) var(--streamvave-spacing-12);
    border-radius: var(--streamvave-border-radius-large);
    transition: var(--streamvave-transition-15s);

    &:active {
        transform: scale(0.97);
    }

    @media(hover: hover) {
        &:hover {
            background-color: var(--streamvave-color-gray-1000);
        }
    }

    @media(hover: none) {
        &:active {
            background-color: var(--streamvave-color-gray-1000);
        }
    }

    &[data-disabled="true"] {
        pointer-events: none;
        color: var(--streamvave-color-gray-600);
        background-color: var(--streamvave-color-gray-300);
    }
}

.input {
    max-width: 100%;
    width: 100%;
    border-radius: var(--streamvave-border-radius-large);
    border: 2px solid var(--streamvave-color-gray-300);
    transition: var(--streamvave-transition-15s);
    cursor: text;
    padding: var(--streamvave-spacing-12) var(--streamvave-spacing-40) var(--streamvave-spacing-12) var(--streamvave-spacing-14);

    &__icon {
        display: flex;
        align-items: center;
        position: absolute;
        top: var(--streamvave-spacing-0);
        right: var(--streamvave-spacing-0);
        bottom: var(--streamvave-spacing-0);
        z-index: var(--streamvave-z-index-10);
        padding: var(--streamvave-spacing-12);
        margin: 2px;
        border-radius: 0 var(--streamvave-border-radius-large) var(--streamvave-border-radius-large) 0;

        svg {
            width: var(--streamvave-spacing-20);
            height: var(--streamvave-spacing-20);
            transition: var(--streamvave-transition-15s);
        }

        &--static {
            color: var(--streamvave-color-gray-600);

            &.hover {
                color: var(--streamvave-color-purple-600);
            }

            &.focus {
                color: var(--streamvave-color-purple-700);
            }
        }

        &--true {
            color: var(--streamvave-color-green-600);
        }

        &--false {
            color: var(--streamvave-color-red-600);
        }

        &--true,
        &--false,
        &--static {
            svg {
                opacity: 0;
            }
        }

        &.active {
            svg {
                opacity: 1;
            }
        }
    }

    &:focus {
        border-color: var(--streamvave-color-gray-700);
    }

    &:hover {
        border-color: var(--streamvave-color-gray-600);
    }

    &::placeholder {
        color: var(--streamvave-color-gray-600);
    }

    &:focus::placeholder {
        color: var(--streamvave-color-white);
    }

    &-true {
        border-color: var(--streamvave-color-green-600);

        &:hover,
        &:focus {
            border-color: var(--streamvave-color-green-600);
        }
    }

    &-false {
        border-color: var(--streamvave-color-red-600);

        &:hover,
        &:focus {
            border-color: var(--streamvave-color-red-600);
        }
    }
}

.switch {
    display: inline-block;
    height: 20px;
    width: 39px;
    position: relative;

    input {
        display: none;
    }

    &-slider {
        cursor: pointer;
        position: absolute;
        z-index: var(--streamvave-z-index-10);
        top: var(--streamvave-spacing-0);
        left: var(--streamvave-spacing-0);
        right: var(--streamvave-spacing-0);
        bottom: var(--streamvave-spacing-0);
        background-color: var(--streamvave-color-black-rgba-015);
        transition: var(--streamvave-transition-15s);

        &:active {
            background-color: var(--streamvave-color-black-rgba-020);
        }

        &::before,
        &::after {
            content: "";
            position: absolute;
            top: var(--streamvave-spacing-0);
            bottom: var(--streamvave-spacing-0);
            transition: var(--streamvave-transition-15s);
            margin-top: auto;
            margin-bottom: auto;
        }

        &:before {
            left: 2px;
            z-index: var(--streamvave-z-index-20);
            width: var(--streamvave-spacing-16);
            height: var(--streamvave-spacing-16);
            background-color: var(--streamvave-color-white);

        }

        &:after {
            right: 7px;
            width: 10px;
            height: 10px;
            border-radius: var(--streamvave-border-radius-full);
            border: 2px solid var(--streamvave-color-white);
        }
    }

    input:checked+.switch-slider {
        background-color: var(--streamvave-color-green-600);

        &:active {
            background-color: var(--streamvave-color-green-700);

        }
    }

    input:checked+.switch-slider:before {
        transform: translateX(19px);
    }

    input:checked+.switch-slider:after {
        width: 1px;
        transform: translateX(-18px);
    }

    .switch-slider.switch-round {
        border-radius: var(--streamvave-border-radius-full);
    }

    .switch-slider.switch-round:before {
        border-radius: var(--streamvave-border-radius-full);
    }
}

.radio-buttons {
    &-wrapper {
        display: grid;
        gap: 8px;
    }

    input {
        display: none;
    }

    label {
        grid-column: auto/span 1;
        width: 100%;
        border-radius: var(--streamvave-border-radius-small);
        border-width: 2px;
        border-style: solid;
        border-color: var(--streamvave-color-gray-300);
        cursor: pointer;
        transition: var(--streamvave-transition-15s);

        &:hover {
            border-color: var(--streamvave-color-green-600);
        }

        &:active {
            border-color: var(--streamvave-color-green-700);
        }
    }

    input:checked+label {
        border-color: var(--streamvave-color-green-600);

        &:active {
            border-color: var(--streamvave-color-green-700);
        }
    }

}