.stream {
    padding: var(--streamvave-spacing-0) var(--streamvave-spacing-0) var(--streamvave-spacing-60);

    &__wrap {
        display: flex;
        flex-direction: column;
        gap: var(--streamvave-spacing-20);
    }

    &-arrangement-element-player-chat {
        &-none {
            .chat {
                display: none;
            }
        }

        &-right {
            .chat {
                display: block;
            }
        }

        &-left {
            .stream__blocks {
                flex-direction: row-reverse;
            }
        }
    }
}

.stream__container {}

.container {}

.stream__blocks {
    display: flex;
    flex-direction: row;
    // display: grid;
    // grid-template-columns: repeat(4, 1fr);
    // grid-template-areas:
    //     "player player player chat"
    // ;
    gap: 20px;
}

.stream__block {
    overflow: hidden;
    background-color: var(--streamvave-color-black-rgba-015);
    border-radius: var(--streamvave-border-radius-medium);
}

.player {
    aspect-ratio: 16/9;
    max-width: 100%;
    width: 100%;
    border-color: initial !important;

    .spoiler {
        svg {
            width: var(--streamvave-spacing-32);
            height: var(--streamvave-spacing-32);
        }
    }

    iframe {
        max-width: 100%;
        width: 100%;
        aspect-ratio: 16/9;
        // margin-left: -1px !important;
    }
}

.chat {
    flex-shrink: 0;
    width: 350px;
    position: relative;

    .spoiler {
        svg {
            width: var(--streamvave-spacing-28);
            height: var(--streamvave-spacing-28);
        }
    }

    iframe {
        // width: 100%;

    }
}

.chat-inner {
    position: absolute;
    top: -35%;
    left: -33%;
    transform: scale(0.6);
    backface-visibility: hidden;
    transform-style: preserve-3d;

}

.spoiler {
    display: none;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    svg {
        color: var(--streamvave-color-white);
    }

    &.active {
        display: flex;
    }
}

.stream__btn {
    max-width: 100%;
    width: 100%;

    svg {
        width: var(--streamvave-spacing-20);
        height: var(--streamvave-spacing-20);
        color: var(--streamvave-color-black);
    }
}

.stream__buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: var(--streamvave-spacing-24);
}

.stream-widget {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: var(--streamvave-spacing-20);

    &__btn {
        width: auto;
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: var(--streamvave-spacing-8);

        &-icon {
            svg {
                width: var(--streamvave-spacing-20);
                height: var(--streamvave-spacing-20);
            }
        }

        &-text {
            font-size: var(--streamvave-font-size-tiny);
            font-weight: var(--streamvave-font-weight-semi-bold);

        }
    }
}