.navigation {
    width: 100%;

    &__wrap {
        display: flex;
        flex-direction: column;
    }

    &__content {
        height: var(--streamvave-spacing-448);
    }
}

.navigation-content {
    &__title {
        font-size: var(--streamvave-font-size-medium);
        font-weight: var(--streamvave-font-weight-medium);
        padding: var(--streamvave-spacing-16) var(--streamvave-spacing-20) var(--streamvave-spacing-16) var(--streamvave-spacing-16);
        border-bottom: 1px solid var(--streamvave-color-black-rgba-005);
    }

    &.noactive {
        display: none;
    }

    &.block {
        display: block;
    }
}

.navigation-content__items {
    display: flex;
    flex-direction: column;
    height: 24.8125rem;
    overflow-x: hidden;
    padding-bottom: var(--streamvave-spacing-24);
}

.navigation-content__item {
    padding: var(--streamvave-spacing-16) var(--streamvave-spacing-20) var(--streamvave-spacing-16) var(--streamvave-spacing-16);
    border-bottom: 1px solid var(--streamvave-color-black-rgba-005);

    &-wrap {}

    &-title {
        display: flex;
        flex-direction: row;
        gap: var(--streamvave-spacing-8);
        font-size: var(--streamvave-font-size-medium);

        svg {
            width: var(--streamvave-spacing-16);
            height: var(--streamvave-spacing-16);
            color: var(--streamvave-color-black);
        }

        span {}
    }

    &-box {}
}

.item-wrap-1 {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.item-wrap-2 {
    display: flex;
    flex-direction: column;
    gap: 18px;
}


.navigation__tabs {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: var(--streamvave-spacing-4);
    padding: var(--streamvave-spacing-4);
    border-radius: 0 0 var(--streamvave-border-radius-large) var(--streamvave-border-radius-large);
    background-color: var(--streamvave-color-white);
    // border-top: 1px solid var(--streamvave-color-black-rgba-005);
    box-shadow: 0 -8px 8px 0px rgba(0, 0, 0, 0.05);
}

.navigation__tab {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: var(--streamvave-spacing-8) var(--streamvave-spacing-20) var(--streamvave-spacing-8) var(--streamvave-spacing-20);
    border-radius: var(--streamvave-border-radius-large);
    cursor: pointer;
    transition: var(--streamvave-transition-15s);

    &.active {
        background-color: var(--streamvave-color-black-rgba-010);

        .navigation__tab-icon-default {
            display: none;
        }

        .navigation__tab-icon-fill {
            display: block;
        }
    }

    &-icon {
        pointer-events: none;
        transition: var(--streamvave-transition-15s);

        svg {
            width: var(--streamvave-spacing-24);
            height: var(--streamvave-spacing-24);
        }

        &-fill {
            display: none;
        }
    }
}

.arrangement-element {
    &__wrap {
        grid-template-columns: repeat(3, 1fr);
    }

    &__item {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: var(--streamvave-spacing-4);
        height: var(--streamvave-spacing-68);
        padding: var(--streamvave-spacing-8);

        svg {
            width: var(--streamvave-spacing-16);
            height: var(--streamvave-spacing-16);
            color: var(--streamvave-color-white);
        }

        &-player,
        &-chat {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;
            background-color: var(--streamvave-color-gray-600);
            border-radius: var(--streamvave-border-radius-extra-small);
        }

        &-player {
            width: 100%;
        }

        &-chat {
            width: 50%;
        }

        &--1 {
            .arrangement-element__item-chat {
                display: none;
            }
        }

        &--2 {}

        &--3 {
            flex-direction: row-reverse;
        }
    }

}

.width-container {
    &__wrap {
        grid-template-columns: repeat(3, 1fr);
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        height: var(--streamvave-spacing-68);
        padding: var(--streamvave-spacing-8);

        svg {
            width: var(--streamvave-spacing-20);
            height: var(--streamvave-spacing-20);
            color: var(--streamvave-color-white);
        }

        &-container {
            display: flex;
            align-items: center;
            justify-content: center;
            // height: var(--streamvave-spacing-28);
            height: 100%;
            @include font(12, 500);
            color: var(--streamvave-color-white);
            background-color: var(--streamvave-color-gray-600);
            border-radius: var(--streamvave-border-radius-extra-small);
        }

        &--1 {
            .width-container__item-container {
                width: 60%;
            }
        }

        &--2 {
            .width-container__item-container {
                width: 80%;
            }
        }

        &--3 {
            .width-container__item-container {
                width: 90%;
            }
        }

        &--4 {
            .width-container__item-container {
                width: 100%;
            }
        }
    }

}