html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}


body {
  font-family: var(--streamvave-font-base);
  font-size: var(--streamvave-font-size-medium);
  background: var(--streamvave-color-gray-100);
  overflow-x: hidden;
  // display: flex;
  // flex-direction: column;
  // justify-content: space-between;
  // min-height: 100vh;

  // @media (prefers-color-scheme: dark) {
  //   background-color: var(--streamvave-color-gray-1100);
  // }
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: var(--streamvave-color-black);
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

button,
input,
label {
  background-color: transparent;
  border: initial;
  outline: none;
}

svg {
  display: flex;
}

* {
  scrollbar-color: var(--streamvave-color-gray-500) transparent;
  scrollbar-width: thin;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 22px;
  background-color: var(--streamvave-color-gray-300);
}

::-webkit-scrollbar {
  width: 6px;
  border: none;
}