.modal {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: fixed;
    padding: var(--streamvave-spacing-64) var(--streamvave-spacing-24);
    top: var(--streamvave-spacing-0);
    left: var(--streamvave-spacing-0);
    right: var(--streamvave-spacing-0);
    bottom: var(--streamvave-spacing-0);
    background-color: var(--streamvave-color-black-rgba-060);
    // backdrop-filter: var(--streamvave-blur-small);
    z-index: var(--streamvave-z-index-modal);
    height: 100dvh;
    overflow-x: hidden;
    transition: var(--streamvave-transition-15s);

    &__wrap {
        max-width: var(--streamvave-spacing-384);
        width: 100%;
        margin: auto 0;
    }

    &-content {
        position: relative;
        border-radius: var(--streamvave-border-radius-large);
        background-color: var(--streamvave-color-white);
        box-shadow: var(--streamvave-box-shadow-small);
        overflow: hidden;
        transition: all .3s cubic-bezier(.4, 0, .2, 1);
    }

    &-header {
        font-size: var(--streamvave-font-size-medium);
        font-weight: var(--streamvave-font-weight-medium);
        padding: var(--streamvave-spacing-16) var(--streamvave-spacing-20);
    }

    &-body {
        &__add-nickname {
            padding: var(--streamvave-spacing-8) var(--streamvave-spacing-20) var(--streamvave-spacing-20) var(--streamvave-spacing-20);
        }
    }

    &-close-btn {
        position: absolute;
        top: var(--streamvave-spacing-4);
        right: var(--streamvave-spacing-4);
        z-index: var(--streamvave-z-index-10);
        cursor: pointer;

        &__wrap {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: var(--streamvave-spacing-8);
            background-color: transparent;
            border-radius: var(--streamvave-border-radius-full);
            overflow: hidden;

            svg {
                width: var(--streamvave-spacing-16);
                height: var(--streamvave-spacing-16);
                color: var(--streamvave-color-gray-700);
            }
        }

        &:hover {
            .modal-close-btn__wrap {
                background-color: var(--streamvave-color-black-rgba-005);
            }
        }

        &:active {
            .modal-close-btn__wrap {
                background-color: var(--streamvave-color-black-rgba-010);
            }
        }
    }

    &__add-nickname {
        &.active {
            pointer-events: all;
            opacity: 1;

            .modal-content {
                transform: scale(1);
            }
        }
    }

    &[data-modal-active="false"] {
        pointer-events: none;
        opacity: 0;

        .modal-content {
            transform: scale(1.03);
        }
    }

    &[data-modal-active="true"] {
        pointer-events: all;
        opacity: 1;

        .modal-content {
            transform: scale(1);
        }
    }
}