.header {
    padding: var(--streamvave-spacing-14) var(--streamvave-spacing-0);

    &__btn {

        padding: var(--streamvave-spacing-12) var(--streamvave-spacing-36);

        svg {
            width: var(--streamvave-spacing-16);
            height: var(--streamvave-spacing-16);
            color: var(--streamvave-color-black);
        }
    }
}


.header__blocks {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__block {}

.logo {
    svg {
        width: 180px;
        height: 28px;
        color: var(--streamvave-color-black);
    }

    // @media (prefers-color-scheme: dark) {
    //     svg {
    //         color: var(--streamvave-color-green-600);
    //     }
    // }
}